html, body {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
header .header-container {
  display: -webkit-flex;
  display: flex;
  padding: 10px 0;
  margin: 0 auto;
  max-width: 1080px;
  width: 100%;
  -webkit-align-items: center;
          align-items: center; }
  header .header-container a.logo {
    display: block;
    margin-right: 25px;
    width: 150px; }
  header .header-container .header-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%; }
    header .header-container .header-wrapper .header-top {
      margin-top: 8px;
      margin-bottom: 8px; }
      header .header-container .header-wrapper .header-top h5 {
        text-transform: uppercase; }
    header .header-container .header-wrapper .header-divider {
      background: #d21034;
      height: 2px;
      width: 100%;
      max-width: 600px;
      margin: 2px 0; }
    header .header-container .header-wrapper nav {
      margin-top: 5px;
      margin-left: -15px; }
      header .header-container .header-wrapper nav a {
